top: 0;\n  left: 0;\n  right: 0;\n  bottom: 0;\n  z-index: 9999;\n  overflow: hidden;\n  background: #fff;\n}\n\n#preloader:before {\n  content: \"\";\n  position: fixed;\n  top: calc(50% - 30px);\n  left: calc(50% - 30px);\n  border: 6px solid #1977cc;\n  border-top-color: #d1e6f9;\n  border-radius: 50%;\n  width: 60px;\n  height: 60px;\n  -webkit-animation: animate-preloader 1s linear infinite;\n  animation: animate-preloader 1s linear infinite;\n}\n\n@-webkit-keyframes animate-preloader {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n@keyframes animate-preloader {\n  0% {\n    transform: rotate(0deg);\n  }\n  100% {\n    transform: rotate(360deg);\n  }\n}\n\n/*--------------------------------------------------------------\n# Back to top button\n--------------------------------------------------------------*/\n.back-to-top {\n  position: fixed;\n  display: none;\n  right: 15px;\n  bottom: 15px;\n  z-index: 99999;\n}\n\n.back-to-top i {\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 24px;\n  width: 40px;\n  height: 40px;\n  border-radius: 50px;\n  background: #1977cc;\n  color: #fff;\n  transition: all 0.4s;\n}\n\n.back-to-top i:hover {\n  background: #1c84e3;\n  color: #fff;\n}\n\n.datepicker-dropdown {\n  padding: 20px !important;\n}\n\n/*--------------------------------------------------------------\n# Top Bar\n--------------------------------------------------------------*/\n#topbar {\n  background: #fff;\n  height: 40px;\n  font-size: 14px;\n  transition: all 0.5s;\n}\n\n#topbar.topbar-scrolled {\n  top: -40px;\n}\n\n#topbar .contact-info a {\n  line-height: 1;\n  color: #444444;\n  transition: 0.3s;\n}\n\n#topbar .contact-info a:hover {\n  color: #1977cc;\n}\n\n#topbar .contact-info i {\n  color: #1977cc;\n  padding-right: 4px;\n  margin-left: 15px;\n}\n\n#topbar .contact-info i:first-child {\n  margin-left: 0;\n}\n\n#topbar .social-links a {\n  color: #437099;\n  padding-left: 15px;\n  display: inline-block;\n  line-height: 1px;\n  transition: 0.3s;\n}\n\n#topbar .social-links a:hover {\n  color: #1977cc;\n}\n\n#topbar .social-links a:first-child {\n  border-left: 0;\n}\n\n/*--------------------------------------------------------------\n# Header\n--------------------------------------------------------------*/\n#header {\n  background: #fff;\n  transition: all 0.5s;\n  z-index: 997;\n  padding: 15px 0;\n  top: 0px;\n  box-shadow: 0px 2px 15px rgba(25, 119, 204, 0.1);\n}\n\n#header.header-scrolled {\n  top: 0;\n}\n\n#header .logo {\n  font-size: 32px;\n  margin: 0;\n  padding: 0;\n  line-height: 1;\n  font-weight: 700;\n  letter-spacing: 0.5px;\n  font-family: \"Poppins\", sans-serif;\n}\n\n#header .logo a {\n  color: #2c4964;\n}\n\n#header .logo img {\n  max-height: 40px;\n}\n\n@media (max-width: 992px) {\n  #header {\n    top: 0;\n  }\n  #header .logo {\n    font-size: 28px;\n  }\n}\n\n/*--------------------------------------------------------------\n# Navigation Menu\n--------------------------------------------------------------*/\n/* Desktop Navigation */\n.nav-menu ul {\n  margin: 0;\n  padding: 0;\n  list-style: none;\n}\n\n.nav-menu > ul {\n  display: flex;\n}\n\n.nav-menu > ul > li {\n  position: relative;\n  white-space: nowrap;\n  padding: 8px 0 8px 20px;\n}\n\n.nav-menu a {\n  display: block;\n  position: relative;\n  color: #2c4964;\n  transition: 0.3s;\n  font-size: 14px;\n  font-family: \"Open Sans\", sans-serif;\n  padding: 5px 2px;\n  border-bottom: 2px solid #fff;\n}\n\n.nav-menu a:hover, .nav-menu .active > a, .nav-menu li:hover > a {\n  color: #1977cc;\n  border-color: #1977cc;\n}\n\n.nav-menu .drop-down ul {\n  display: block;\n  position: absolute;\n  left: 20px;\n  top: calc(100% + 30px);\n  z-index: 99;\n  opacity: 0;\n  visibility: hidden;\n  padding: 10px 0;\n  background: #fff;\n  box-shadow: 0px 0px 30px rgba(127, 137, 161, 0.25);\n  transition: 0.3s;\n}\n\n.nav-menu .drop-down:hover > ul {\n  opacity: 1;\n  top: 100%;\n  visibility: visible;\n}\n\n.nav-menu .drop-down li {\n  min-width: 180px;\n  position: relative;\n}\n\n.nav-menu .drop-down ul a {\n  padding: 10px 20px;\n  font-size: 14px;\n  font-weight: 500;\n  text-transform: none;\n  color: #082744;\n  border: none;\n}\n\n.nav-menu .drop-down ul a:hover, .nav-menu .drop-down ul .active > a, .nav-menu .drop-down ul li:hover > a {\n  color: #1977cc;\n}\n\n.nav-menu .drop-down > a:after {\n  content: \"\\ea99\";\n  font-family: IcoFont;\n  padding-left: 5px;\n}\n\n.nav-menu .drop-down .drop-down ul {\n  top: 0;\n  left: calc(100% - 30px);\n}\n\n.nav-menu .drop-down .drop-down:hover > ul {\n  opacity: 1;\n  top: 0;\n  left: 100%;\n}\n\n.nav-menu .drop-down .drop-down > a {\n  padding-right: 35px;\n}\n\n.nav-menu .drop-down .drop-down > a:after {\n  content: \"\\eaa0\";\n  font-family: IcoFont;\n  position: absolute;\n  right: 15px;\n}\n\n@media (max-width: 1366px) {\n  .nav-menu .drop-down .drop-down ul {\n    left: -90%;\n  }\n  .nav-menu .drop-down .drop-down:hover > ul {\n    left: -100%;\n  }\n  .nav-menu .drop-down .drop-down > a:after {\n    content: \"\\ea9d\";\n  }\n}\n\n/* Get Startet Button */\n.appointment-btn {\n  margin-left: 25px;\n  background: #1977cc;\n  color: #fff;\n  border-radius: 50px;\n  padding: 8px 25px;\n  white-space: nowrap;\n  transition: 0.3s;\n  font-size: 14px;\n  display: inline-block;\n}\n\n.appointment-btn:hover {\n  background: #166ab5;\n  color: #fff;\n}\n\n@media (max-width: 768px) {\n  .appointment-btn {\n    margin: 0 48px 0 0;\n    padding: 6px 18px;\n  }\n}\n\n/* Mobile Navigation */\n.mobile-nav-toggle {\n  position: fixed;\n  right: 15px;\n  top: 15px;\n  z-index: 9998;\n  border: 0;\n  background: none;\n  font-size: 24px;\n  transition: all 0.4s;\n  outline: none !important;\n  line-height: 1;\n  cursor: pointer;\n  text-align: right;\n}\n\n.mobile-nav-toggle i {\n  color: #1977cc;\n}\n\n.mobile-nav {\n  position: fixed;\n  top: 55px;\n  right: 15px;\n  bottom: 15px;\n