amily: \"Raleway\", sans-serif;\n  text-transform: uppercase;\n  font-weight: 500;\n  font-size: 14px;\n  letter-spacing: 1px;\n  display: inline-block;\n  padding: 12px 35px;\n  margin-top: 30px;\n  border-radius: 50px;\n  transition: 0.5s;\n  color: #fff;\n  background: #1977cc;\n}\n\n#hero .btn-get-started:hover {\n  background: #3291e6;\n}\n\n@media (min-width: 1024px) {\n  #hero {\n    background-attachment: fixed;\n  }\n}\n\n@media (max-width: 992px) {\n  #hero {\n    margin-bottom: 0;\n    height: 100vh;\n  }\n  #hero .container {\n    padding-bottom: 63px;\n  }\n  #hero h1 {\n    font-size: 28px;\n    line-height: 36px;\n  }\n  #hero h2 {\n    font-size: 18px;\n    line-height: 24px;\n    margin-bottom: 30px;\n  }\n}\n\n@media (max-height: 500px) {\n  #hero {\n    height: auto;\n  }\n}\n\n/*--------------------------------------------------------------\n# Sections General\n--------------------------------------------------------------*/\nsection {\n  padding: 60px 0;\n  overflow: hidden;\n}\n\n.section-bg {\n  background-color: #f1f7fd;\n}\n\n.section-title {\n  text-align: center;\n  padding-bottom: 30px;\n}\n\n.section-title h2 {\n  font-size: 32px;\n  font-weight: bold;\n  margin-bottom: 20px;\n  padding-bottom: 20px;\n  position: relative;\n  color: #2c4964;\n}\n\n.section-title h2::before {\n  content: '';\n  position: absolute;\n  display: block;\n  width: 120px;\n  height: 1px;\n  background: #ddd;\n  bottom: 1px;\n  left: calc(50% - 60px);\n}\n\n.section-title h2::after {\n  content: '';\n  position: absolute;\n  display: block;\n  width: 40px;\n  height: 3px;\n  background: #1977cc;\n  bottom: 0;\n  left: calc(50% - 20px);\n}\n\n.section-title p {\n  margin-bottom: 0;\n}\n\n/*--------------------------------------------------------------\n# Breadcrumbs\n--------------------------------------------------------------*/\n.breadcrumbs {\n  padding: 20px 0;\n  background-color: #f1f7fd;\n  min-height: 40px;\n  margin-top: 120px;\n}\n\n@media (max-width: 